<template>
  <section class="tab-content">
    <div class="bg-white p-6 rounded-lg shadow">
      <div class="flex justify-between items-center mb-4">
        <h2 class="text-2xl font-semibold">üí∏ Expenses</h2>
        <button @click="openAddModal" class="bg-accent text-white px-4 py-2 rounded hover:bg-emerald-600">
          + Add Expense
        </button>
      </div>
      <div class="overflow-x-auto">
        <div class="flex flex-col sm:flex-row sm:items-center sm:justify-between mb-4 space-y-2 sm:space-y-0">
          <input type="text" v-model="searchQuery" placeholder="üîç Search expense..."
            class="border rounded px-3 py-2 w-full sm:w-64" />
          <div class="text-sm text-gray-600">
            Page {{ currentPage }} of {{ totalPages }}
          </div>
        </div>
        <table
          class="min-w-full text-sm text-left text-gray-700 border border-gray-200 shadow-sm rounded-xl overflow-hidden">
          <thead class="text-xs uppercase bg-gray-100 text-gray-600">
            <tr>
              <th class="px-4 py-3">Date</th>
              <th class="px-4 py-3">Category</th>
              <th class="px-4 py-3">Description</th>
              <th class="px-4 py-3">Amount</th>
              <th class="px-4 py-3">Actions</th>
            </tr>
          </thead>
          <tbody class="bg-white divide-y divide-gray-200">
            <tr v-for="(expense, index) in paginatedExpenses" :key="index" class="hover:bg-gray-50 transition-all">
              <td class="px-4 py-3">{{ expense.date }}</td>
              <td class="px-4 py-3">{{ expense.category }}</td>
              <td class="px-4 py-3">{{ expense.description }}</td>
              <td class="px-4 py-3 font-medium text-gray-900">‚Çπ{{ expense.amount }}</td>
              <td class="px-4 py-3 space-x-2">
                <button @click="openEditModal(expense, index)"
                  class="inline-flex items-center px-2 py-1 text-yellow-600 hover:underline hover:text-yellow-800">
                  ‚úèÔ∏è Edit
                </button>
                <button @click="openDeleteModal(index)"
                  class="inline-flex items-center px-2 py-1 text-red-600 hover:underline hover:text-red-800">
                  ‚ùå Delete
                </button>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="mt-4 flex justify-center space-x-2">
          <button @click="currentPage--" :disabled="currentPage === 1"
            class="px-3 py-1 border rounded hover:bg-gray-100 disabled:opacity-50">
            Prev
          </button>
          <button @click="currentPage++" :disabled="currentPage === totalPages"
            class="px-3 py-1 border rounded hover:bg-gray-100 disabled:opacity-50">
            Next
          </button>
        </div>

      </div>
    </div>

    <!-- Modals -->
    <AddEditModal v-if="modals.add || modals.edit" title="Expense" :isEdit="modals.edit" :form="form" :fields="[
      { key: 'date', label: 'Date', type: 'date', required: true },
      { key: 'category', label: 'Category', type: 'select', options: expenseCategories, required: true },
      { key: 'description', label: 'Description' },
      { key: 'amount', label: 'Amount', type: 'number', required: true }
    ]" @submit="modals.add ? addExpense : updateExpense" @close="closeModals" />

    <DeleteModal v-if="modals.delete" title="Expense" @confirm="deleteExpense" @close="closeModals" />
  </section>
</template>

<script>
import AddEditModal from '@/components/AddEditModal.vue';
import DeleteModal from '@/components/DeleteModal.vue';

export default {
  name: 'ExpensesView',
  components: { AddEditModal, DeleteModal },
  data() {
    return {
      searchQuery: '',
      currentPage: 1,
      itemsPerPage: 5,
      expenses: [
        { date: '2025-03-01', category: 'Electricity', description: 'March bill', amount: 3500 },
        { date: '2025-03-05', category: 'Maintenance', description: 'Plumbing repair', amount: 1200 },
        { date: '2025-03-10', category: 'Supplies', description: 'Cleaning supplies', amount: 800 },
        { date: '2025-03-12', category: 'Internet', description: 'WiFi charges', amount: 1500 },
        { date: '2025-03-15', category: 'Groceries', description: 'Hostel food', amount: 6500 }
      ],
      form: { date: '', category: '', description: '', amount: '' },
      expenseCategories: [
        'Electricity',
        'Water',
        'Internet',
        'Maintenance',
        'Cleaning Supplies',
        'Groceries',
        'Staff Salaries',
        'Security Services',
        'Repairs',
        'Laundry Services',
        'Furniture & Fixtures',
        'Stationery',
        'Medical Supplies',
        'Waste Management',
        'Pest Control',
        'Gardening',
        'Gas Cylinders',
        'Transport',
        'Appliance Maintenance',
        'Subscription Services',
        'IT & Software',
        'Legal & Compliance',
        'Insurance',
        'Printing & Photocopy',
        'Marketing & Advertising',
        'Training & Workshops',
        'Guest Hospitality',
        'Bank Charges',
        'Miscellaneous'
      ],
      selectedIndex: null,
      modals: { add: false, edit: false, delete: false }
    };
  },
  computed: {
    filteredExpenses() {
      return this.expenses.filter(e =>
        e.category.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
        e.description.toLowerCase().includes(this.searchQuery.toLowerCase())
      );
    },
    paginatedExpenses() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      return this.filteredExpenses.slice(start, start + this.itemsPerPage);
    },
    totalPages() {
      return Math.max(1, Math.ceil(this.filteredExpenses.length / this.itemsPerPage));
    }
  },
  watch: {
    searchQuery() {
      this.currentPage = 1;
    }
  },
  methods: {
    openAddModal() { this.resetForm(); this.modals.add = true; },
    openEditModal(data, index) { this.form = { ...data }; this.selectedIndex = index; this.modals.edit = true; },
    openDeleteModal(index) { this.selectedIndex = index; this.modals.delete = true; },
    addExpense(formData) { this.expenses.push({ ...formData }); this.closeModals(); },
    updateExpense(formData) { this.expenses[this.selectedIndex] = { ...formData }; this.closeModals(); },
    deleteExpense() { this.expenses.splice(this.selectedIndex, 1); this.closeModals(); },
    closeModals() { this.modals = { add: false, edit: false, delete: false }; },
    resetForm() {
      this.form = { date: '', category: '', description: '', amount: '' };
    }
  }
};
</script>

<style scoped>
.tab-content {
  animation: fadeIn 0.3s ease-in-out;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
</style>
