<template>
  <section class="tab-content">
    <div class="bg-white p-6 rounded-lg shadow">
      <div class="flex justify-between items-center mb-4">
        <h2 class="text-2xl font-semibold">üë®‚Äçüíº Staff</h2>
        <button @click="openAddModal" class="bg-accent text-white px-4 py-2 rounded hover:bg-emerald-600">
          + Add Staff
        </button>
      </div>
      <div class="overflow-x-auto">
        <div class="flex flex-col sm:flex-row sm:items-center sm:justify-between mb-4 space-y-2 sm:space-y-0">
          <input type="text" v-model="searchQuery" placeholder="üîç Search staff..."
            class="border rounded px-3 py-2 w-full sm:w-64" />
          <div class="text-sm text-gray-600">
            Page {{ currentPage }} of {{ totalPages }}
          </div>
        </div>
        <table
          class="min-w-full text-sm text-left text-gray-700 border border-gray-200 shadow-sm rounded-xl overflow-hidden">
          <thead class="text-xs uppercase bg-gray-100 text-gray-600">
            <tr>
              <th class="px-4 py-3">Name</th>
              <th class="px-4 py-3">Phone</th>
              <th class="px-4 py-3">Email</th>
              <th class="px-4 py-3">Role</th>
              <th class="px-4 py-3">Joined</th>
              <th class="px-4 py-3">Actions</th>
            </tr>
          </thead>
          <tbody class="bg-white divide-y divide-gray-200">
            <tr v-for="(staff, index) in paginatedStaff" :key="index" class="hover:bg-gray-50 transition-all">
              <td class="px-4 py-3 font-medium text-gray-900">{{ staff.name }}</td>
              <td class="px-4 py-3">{{ staff.phone }}</td>
              <td class="px-4 py-3">{{ staff.email }}</td>
              <td class="px-4 py-3">{{ staff.role }}</td>
              <td class="px-4 py-3">{{ staff.joined }}</td>
              <td class="px-4 py-3 space-x-2">
                <button @click="openViewModal(staff)"
                  class="inline-flex items-center px-2 py-1 text-blue-600 hover:underline hover:text-blue-800">
                  üîç View
                </button>
                <button @click="openEditModal(staff, index)"
                  class="inline-flex items-center px-2 py-1 text-yellow-600 hover:underline hover:text-yellow-800">
                  ‚úèÔ∏è Edit
                </button>
                <button @click="openDeleteModal(index)"
                  class="inline-flex items-center px-2 py-1 text-red-600 hover:underline hover:text-red-800">
                  ‚ùå Delete
                </button>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="mt-4 flex justify-center space-x-2">
          <button @click="currentPage--" :disabled="currentPage === 1"
            class="px-3 py-1 border rounded hover:bg-gray-100 disabled:opacity-50">
            Prev
          </button>
          <button @click="currentPage++" :disabled="currentPage === totalPages"
            class="px-3 py-1 border rounded hover:bg-gray-100 disabled:opacity-50">
            Next
          </button>
        </div>

      </div>
    </div>

    <!-- Modals -->
    <ViewModal v-if="modals.view" title="Staff" :data="selectedStaff" :fields="{
      name: 'Name',
      phone: 'Phone',
      email: 'Email',
      role: 'Role',
      joined: 'Joined Date'
    }" @close="closeModals" />

    <AddEditModal v-if="modals.add || modals.edit" title="Staff" :isEdit="modals.edit" :form="form" :fields="[
      { key: 'name', label: 'Name', required: true },
      { key: 'phone', label: 'Phone', required: true },
      { key: 'email', label: 'Email', type: 'email' },
      { key: 'role', label: 'Role' },
      { key: 'joined', label: 'Joined Date', type: 'date' }
    ]" @submit="modals.add ? addStaff : updateStaff" @close="closeModals" />

    <DeleteModal v-if="modals.delete" title="Staff" @confirm="deleteStaff" @close="closeModals" />
  </section>
</template>

<script>
import ViewModal from '@/components/ViewModal.vue';
import AddEditModal from '@/components/AddEditModal.vue';
import DeleteModal from '@/components/DeleteModal.vue';

export default {
  name: 'StaffView',
  components: { ViewModal, AddEditModal, DeleteModal },
  data() {
    return {
      searchQuery: '',
      currentPage: 1,
      itemsPerPage: 5,
      staffList: [
        { name: 'Ajay Sharma', phone: '9990001111', email: 'ajay@example.com', role: 'Manager', joined: '2024-05-01' },
        { name: 'Neha Singh', phone: '9990001112', email: 'neha@example.com', role: 'Receptionist', joined: '2024-06-15' },
        { name: 'Vikas Rao', phone: '9990001113', email: 'vikas@example.com', role: 'Cleaner', joined: '2024-07-01' },
        { name: 'Kiran Patil', phone: '9990001114', email: 'kiran@example.com', role: 'Security', joined: '2024-08-20' },
        { name: 'Alok Verma', phone: '9990001115', email: 'alok@example.com', role: 'Cook', joined: '2024-09-10' }
      ],
      form: { name: '', phone: '', email: '', role: '', joined: '' },
      selectedStaff: {},
      selectedIndex: null,
      modals: { add: false, edit: false, view: false, delete: false }
    };
  },
  computed: {
    filteredStaff() {
      return this.staffList.filter(s =>
        s.name.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
        s.phone.includes(this.searchQuery) ||
        s.email.toLowerCase().includes(this.searchQuery.toLowerCase())
      );
    },
    paginatedStaff() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      return this.filteredStaff.slice(start, start + this.itemsPerPage);
    },
    totalPages() {
      return Math.max(1, Math.ceil(this.filteredStaff.length / this.itemsPerPage));
    }
  },
  watch: {
    searchQuery() {
      this.currentPage = 1;
    }
  },
  methods: {
    openAddModal() { this.resetForm(); this.modals.add = true; },
    openViewModal(data) { this.selectedStaff = data; this.modals.view = true; },
    openEditModal(data, index) { this.form = { ...data }; this.selectedIndex = index; this.modals.edit = true; },
    openDeleteModal(index) { this.selectedIndex = index; this.modals.delete = true; },
    addStaff(formData) { this.staffList.push({ ...formData }); this.closeModals(); },
    updateStaff(formData) { this.staffList[this.selectedIndex] = { ...formData }; this.closeModals(); },
    deleteStaff() { this.staffList.splice(this.selectedIndex, 1); this.closeModals(); },
    closeModals() { this.modals = { add: false, edit: false, view: false, delete: false }; },
    resetForm() {
      this.form = { name: '', phone: '', email: '', role: '', joined: '' };
    }
  }
};
</script>

<style scoped>
.tab-content {
  animation: fadeIn 0.3s ease-in-out;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
</style>