<template>
    <div class="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center z-50">
      <div class="bg-white p-6 rounded-lg w-full max-w-lg">
        <h3 class="text-xl font-semibold mb-4">{{ isEdit ? 'Edit' : 'Add' }} {{ title }}</h3>
        <form @submit.prevent="$emit('submit', localForm)">
          <div class="space-y-4">
            <div v-for="field in fields" :key="field.key">
              <input
                v-model="localForm[field.key]"
                :type="field.type || 'text'"
                :placeholder="field.placeholder || field.label"
                class="w-full p-2 border rounded"
                :required="field.required"
              />
            </div>
          </div>
          <div class="flex justify-end space-x-2 mt-4">
            <button type="button" @click="$emit('close')" class="px-4 py-2 bg-gray-300 rounded">Cancel</button>
            <button type="submit" class="px-4 py-2 bg-accent text-white rounded">
              {{ isEdit ? 'Update' : 'Save' }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    props: {
      title: String,
      isEdit: Boolean,
      form: Object,
      fields: Array
    },
    data() {
      return {
        localForm: { ...this.form }
      };
    },
    watch: {
      form: {
        handler(newVal) {
          this.localForm = { ...newVal };
        },
        deep: true,
        immediate: true
      }
    }
  };
  </script>
  