<template>
  <section class="tab-content">
    <div class="bg-white p-6 rounded-lg shadow">
      <div class="flex justify-between items-center mb-4">
        <h2 class="text-2xl font-semibold">üë• Tenants</h2>
        <button @click="openAddModal" class="bg-accent text-white px-4 py-2 rounded hover:bg-emerald-600">
          + Add Tenant
        </button>
      </div>
      <div class="overflow-x-auto">
        <div class="flex flex-col sm:flex-row sm:items-center sm:justify-between mb-4 space-y-2 sm:space-y-0">
          <input type="text" v-model="searchQuery" placeholder="üîç Search tenant..."
            class="border rounded px-3 py-2 w-full sm:w-64" />
          <div class="text-sm text-gray-600">
            Page {{ currentPage }} of {{ totalPages }}
          </div>
        </div>
        <table
          class="min-w-full text-sm text-left text-gray-700 border border-gray-200 shadow-sm rounded-xl overflow-hidden">
          <thead class="text-xs uppercase bg-gray-100 text-gray-600">
            <tr>
              <th class="px-4 py-3">Name</th>
              <th class="px-4 py-3">Phone</th>
              <th class="px-4 py-3">Email</th>
              <th class="px-4 py-3">Occupation</th>
              <th class="px-4 py-3">Joined</th>
              <th class="px-4 py-3">Emergency Contact</th>
              <th class="px-4 py-3">Actions</th>
            </tr>
          </thead>
          <tbody class="bg-white divide-y divide-gray-200">
            <tr v-for="(tenant, index) in paginatedTenants" :key="index" class="hover:bg-gray-50 transition-all">
              <td class="px-4 py-3 font-medium text-gray-900">{{ tenant.name }}</td>
              <td class="px-4 py-3">{{ tenant.phone }}</td>
              <td class="px-4 py-3">{{ tenant.email }}</td>
              <td class="px-4 py-3">{{ tenant.occupation }}</td>
              <td class="px-4 py-3">{{ tenant.joined }}</td>
              <td class="px-4 py-3">{{ tenant.emergency }}</td>
              <td class="px-4 py-3 space-x-2">
                <button @click="openViewModal(tenant)"
                  class="inline-flex items-center px-2 py-1 text-blue-600 hover:underline hover:text-blue-800">
                  üîç View
                </button>
                <button @click="openEditModal(tenant, index)"
                  class="inline-flex items-center px-2 py-1 text-yellow-600 hover:underline hover:text-yellow-800">
                  ‚úèÔ∏è Edit
                </button>
                <button @click="openDeleteModal(index)"
                  class="inline-flex items-center px-2 py-1 text-red-600 hover:underline hover:text-red-800">
                  ‚ùå Delete
                </button>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="mt-4 flex justify-center space-x-2">
          <button @click="currentPage--" :disabled="currentPage === 1"
            class="px-3 py-1 border rounded hover:bg-gray-100 disabled:opacity-50">
            Prev
          </button>
          <button @click="currentPage++" :disabled="currentPage === totalPages"
            class="px-3 py-1 border rounded hover:bg-gray-100 disabled:opacity-50">
            Next
          </button>
        </div>

      </div>
    </div>

    <!-- Modals -->
    <ViewModal v-if="modals.view" title="Tenant" :data="selectedTenant" :fields="{
      name: 'Name',
      phone: 'Phone',
      email: 'Email',
      occupation: 'Occupation',
      joined: 'Joined Date',
      emergency: 'Emergency Contact'
    }" @close="closeModals" />

    <AddEditModal v-if="modals.add || modals.edit" title="Tenant" :isEdit="modals.edit" :form="form" :fields="[
      { key: 'name', label: 'Name', required: true },
      { key: 'phone', label: 'Phone', required: true },
      { key: 'email', label: 'Email', type: 'email' },
      { key: 'occupation', label: 'Occupation' },
      { key: 'joined', label: 'Joined Date', type: 'date' },
      { key: 'emergency', label: 'Emergency Contact' }
    ]" @submit="modals.add ? addTenant : updateTenant" @close="closeModals" />

    <DeleteModal v-if="modals.delete" title="Tenant" @confirm="deleteTenant" @close="closeModals" />
  </section>
</template>

<script>
import ViewModal from '@/components/ViewModal.vue';
import AddEditModal from '@/components/AddEditModal.vue';
import DeleteModal from '@/components/DeleteModal.vue';

export default {
  name: 'TenantsView',
  components: { ViewModal, AddEditModal, DeleteModal },
  data() {
    return {
      searchQuery: '',
      currentPage: 1,
      itemsPerPage: 5,
      tenants: [
        {
          name: 'Ravi Kumar',
          phone: '9876543210',
          email: 'ravi@example.com',
          occupation: 'Engineer',
          joined: '2024-11-01',
          emergency: 'Raj (Father) - 9871234567'
        },
        {
          name: 'Sneha Sharma',
          phone: '9876543211',
          email: 'sneha@example.com',
          occupation: 'Designer',
          joined: '2024-12-10',
          emergency: 'Amit (Brother) - 9876543219'
        },
        {
          name: 'Arjun Mehta',
          phone: '9876543212',
          email: 'arjun@example.com',
          occupation: 'Accountant',
          joined: '2025-01-05',
          emergency: 'Sunil (Friend) - 9871234568'
        },
        {
          name: 'Priya Das',
          phone: '9876543213',
          email: 'priya@example.com',
          occupation: 'Teacher',
          joined: '2024-10-22',
          emergency: 'Neha (Sister) - 9876543216'
        },
        {
          name: 'Vikram Sen',
          phone: '9876543214',
          email: 'vikram@example.com',
          occupation: 'Developer',
          joined: '2024-09-14',
          emergency: 'Deepak (Cousin) - 9871234569'
        },
        {
          name: 'Nikita Rao',
          phone: '9876543215',
          email: 'nikita@example.com',
          occupation: 'Student',
          joined: '2025-02-01',
          emergency: 'Manoj (Father) - 9871234570'
        },
        {
          name: 'Kunal Verma',
          phone: '9876543216',
          email: 'kunal@example.com',
          occupation: 'Analyst',
          joined: '2024-08-19',
          emergency: 'Rina (Wife) - 9876543220'
        },
        {
          name: 'Anjali Nair',
          phone: '9876543217',
          email: 'anjali@example.com',
          occupation: 'Nurse',
          joined: '2025-03-01',
          emergency: 'Rajeev (Brother) - 9871234571'
        },
        {
          name: 'Mohit Jain',
          phone: '9876543218',
          email: 'mohit@example.com',
          occupation: 'Lawyer',
          joined: '2024-11-30',
          emergency: 'Reena (Sister) - 9871234572'
        },
        {
          name: 'Pooja Singh',
          phone: '9876543219',
          email: 'pooja@example.com',
          occupation: 'HR Manager',
          joined: '2025-01-20',
          emergency: 'Suresh (Uncle) - 9871234573'
        }
      ]
      ,
      form: {
        name: '', phone: '', email: '', occupation: '', joined: '', emergency: ''
      },
      selectedTenant: {},
      selectedIndex: null,
      modals: {
        add: false,
        edit: false,
        view: false,
        delete: false
      }
    };
  },
  computed: {
    filteredTenants() {
      return this.tenants.filter(t =>
        t.name.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
        t.phone.includes(this.searchQuery) ||
        t.email.toLowerCase().includes(this.searchQuery.toLowerCase())
      );
    },
    paginatedTenants() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      return this.filteredTenants.slice(start, start + this.itemsPerPage);
    },
    totalPages() {
      return Math.ceil(this.filteredTenants.length / this.itemsPerPage);
    }
  },
  methods: {
    openAddModal() { this.resetForm(); this.modals.add = true; },
    openViewModal(data) { this.selectedTenant = data; this.modals.view = true; },
    openEditModal(data, index) { this.form = { ...data }; this.selectedIndex = index; this.modals.edit = true; },
    openDeleteModal(index) { this.selectedIndex = index; this.modals.delete = true; },
    addTenant(formData) { this.tenants.push({ ...formData }); this.closeModals(); },
    updateTenant(formData) { this.tenants[this.selectedIndex] = { ...formData }; this.closeModals(); },
    deleteTenant() { this.tenants.splice(this.selectedIndex, 1); this.closeModals(); },
    closeModals() { this.modals = { add: false, edit: false, view: false, delete: false }; },
    resetForm() {
      this.form = {
        name: '', phone: '', email: '', occupation: '', joined: '', emergency: ''
      };
    }
  }
};
</script>

<style scoped>
.tab-content {
  animation: fadeIn 0.3s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}
</style>
