<template>
    <section class="tab-content">
        <div class="bg-white p-6 rounded-lg shadow">
            <h2 class="text-2xl font-semibold mb-4">üí∏ Tenant Payments</h2>
            <div class="overflow-x-auto">
                <table class="min-w-full text-sm text-left text-gray-600">
                    <thead class="text-xs uppercase bg-gray-100 text-gray-700">
                        <tr>
                            <th class="px-4 py-2">Name</th>
                            <th class="px-4 py-2">Phone</th>
                            <th class="px-4 py-2">Email</th>
                            <th class="px-4 py-2">Occupation</th>
                            <th class="px-4 py-2">Actions</th>
                        </tr>
                    </thead>
                    <tbody class="bg-white divide-y">
                        <tr v-for="(tenant, index) in tenants" :key="index">
                            <td class="px-4 py-2">{{ tenant.name }}</td>
                            <td class="px-4 py-2">{{ tenant.phone }}</td>
                            <td class="px-4 py-2">{{ tenant.email }}</td>
                            <td class="px-4 py-2">{{ tenant.occupation }}</td>
                            <td class="px-4 py-2">
                                <button @click="togglePayment(index)"
                                    class="bg-emerald-500 text-white px-3 py-1 rounded hover:bg-emerald-600">
                                    {{ paymentIndex === index ? 'Close' : 'Add Payment' }}
                                </button>
                            </td>
                        </tr>

                        <!-- Expandable Payment Row -->
                        <tr v-if="paymentIndex === index" class="bg-gray-50">
                            <td colspan="5" class="px-4 py-4">
                                <div class="grid grid-cols-1 md:grid-cols-2 gap-4">
                                    <!-- Add Payment -->
                                    <div>
                                        <h3 class="text-lg font-semibold mb-2">‚ûï Add Payment</h3>
                                        <div class="flex items-center gap-2">
                                            <input v-model="paymentForm.amount" type="number" placeholder="Amount"
                                                class="border px-2 py-1 rounded w-24" />
                                            <input v-model="paymentForm.date" type="date"
                                                class="border px-2 py-1 rounded" />
                                            <button @click="addPayment(index)"
                                                class="bg-blue-500 text-white px-3 py-1 rounded hover:bg-blue-600">
                                                Save
                                            </button>
                                        </div>
                                    </div>

                                    <!-- Payment History -->
                                    <div>
                                        <h3 class="text-lg font-semibold mb-2">üìú Last 10 Payments</h3>
                                        <ul class="text-sm list-disc list-inside">
                                            <li v-for="(p, pIndex) in (tenant.payments || []).slice(-10).reverse()"
                                                :key="pIndex">
                                                ‚Çπ{{ p.amount }} on {{ p.date }}
                                            </li>
                                            <li v-if="!tenant.payments || tenant.payments.length === 0">
                                                No payments found.
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </section>
</template>
<script>
export default {
    name: 'PaymentsView',
    data() {
        return {
            tenants: [
                {
                    name: 'Ravi Kumar',
                    phone: '9876543210',
                    email: 'ravi@example.com',
                    occupation: 'Engineer',
                    payments: [
                        { amount: 5000, date: '2025-03-01' },
                        { amount: 5000, date: '2025-02-01' }
                    ]
                },
                {
                    name: 'Sneha Sharma',
                    phone: '9876543211',
                    email: 'sneha@example.com',
                    occupation: 'Designer',
                    payments: []
                },
                // Add more tenant objects here...
            ],
            paymentIndex: null,
            paymentForm: {
                amount: '',
                date: ''
            }
        };
    },
    methods: {
        togglePayment(index) {
            if (this.paymentIndex === index) {
                this.paymentIndex = null;
            } else {
                this.paymentIndex = index;
                this.paymentForm = { amount: '', date: '' };
            }
        },
        addPayment(index) {
            if (!this.paymentForm.amount || !this.paymentForm.date) {
                alert('Please enter amount and date.');
                return;
            }
            if (!this.tenants[index].payments) {
                this.$set(this.tenants[index], 'payments', []);
            }
            this.tenants[index].payments.push({ ...this.paymentForm });
            this.paymentForm = { amount: '', date: '' };
        }
    }
};
</script>

<style scoped>
@keyframes slideDown {
    from {
        opacity: 0;
        transform: translateY(-5px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.bg-gray-50 {
    animation: slideDown 0.2s ease-in-out;
}
</style>